import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { PageHeader, } from 'antd';
// import { checkPermissions } from '@/components/utils/fun'
// import { Icon } from '@/components'

@inject(({ appStore, authStore }) => ({
  permissions: authStore.permissions,
  addTab: appStore.addTab
}))
@observer
class ComView extends Component {

  handleBack = e => {
    const { addTab, backTitle, backUrl, moduleName } = this.props
    if (backUrl) {
      addTab({
        title: `${backTitle}`,
        url: `${backUrl}`,
        module: moduleName
      })
    }
  };

  render() {
    const { title, showBack } = this.props
    return (
      <PageHeader
        ghost={true}
        onBack={showBack ? this.handleBack : false}
        title={title}
        className="page-title"
      />
    )
  }
};

export default ComView;
